.theme-selector{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-width: 1200px;
    margin: 20px auto;
   margin-right: 20px;
}

.theme-buttons div {
    display: inline-block;
    width: 20px;
    height: 20px;
    cursor: pointer;
    margin-left: 15px;
    border-radius: 50%;
}

.mode-toggle{
    margin-right: auto ;
    margin-left: 20px;

}
.mode-toggle img {
    width: 24px;
    height :24px;
    cursor: pointer;
}